export default {
	data() {
		return {
			couponDetail: null,
			noClick:false,
			couponId:null,
			type:1
		}
	},
	onLoad(options) {
		this.type = options.type
		this.couponDetail = uni.getStorageSync('couponDetail')
	},
	methods: {
		exchange(type) {
			if(type==1){
				if (this.noClick) {
					this.showToast('请勿重复点击')
					return
				}
				this.noClick = true
				this.setheader()
				uni.request({
					url: this.websiteUrl + '/api/coupon/couponPay',
					data: {
						token: uni.getStorageSync('token'),
						id: this.couponDetail.id,
						type: 'coupon'
					},
					header: this.header,
					method: 'POST',
					success: (res) => {
						console.log(res.data)
						if (res.data.status == 0) {
							uni.requestPayment({
								provider: 'wxpay',
								timeStamp: res.data.data.timeStamp,
								nonceStr: res.data.data.nonceStr,
								package: res.data.data.package,
								signType: res.data.data.signType,
								paySign: res.data.data.paySign,
								success: (res) => {
									this.noClick = false
									console.log('success:' + JSON.stringify(res));
									uni.showToast({
										title: "兑换成功",
										icon: 'success'
									})
								},
								fail: (err) => {
									this.noClick = false
									console.log('fail:' + JSON.stringify(err));
								}
							});
							return
						}
						this.showToast(res.data.msg)
					}
				})
			}else if(type==2){
				this.setheader()
				uni.request({
					url: this.websiteUrl + '/api/coupon/getCoupon',
					data: {
						token: uni.getStorageSync('token'),
						id: this.couponDetail.id
					},
					header: this.header,
					success: (res) => {
						console.log(res.data)
						if (res.data.status == 0) {
							uni.navigateTo({
								url:"../exchangeSuccess/exchangeSuccess"
							})
							return
						}
						this.showToast(res.data.msg)
					}
				})
			}
		},
		//处理下单时间
		handleTime(time) {
			let date = new Date(time * 1000)
			let Y = date.getFullYear().toString()
			let M = (date.getMonth() + 1).toString().padStart(2, '0')
			let D = date.getDate().toString().padStart(2, '0')
			// let h = date.getHours().toString().padStart(2, '0')
			// let m = date.getMinutes().toString().padStart(2, '0')
			// let s = date.getSeconds().toString().padStart(2, '0')
			// let newTime = Y + '-' + M + '-' + D + ' ' + h + ':' + m + ':' + s
			let newTime = Y + '.' + M + '.' + D
			return newTime
		}
	}
}
